  margin: 3rem 0 0 0;
  padding: 1rem 0 2rem 0; }

.bloques {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem; }

@media (min-width: 768px) {
  .bloques {
    grid-template-columns: repeat(4, 1fr); } }

.bloque {
  text-align: center; }

.bloque__titulo {
  margin: 0; }

.bloque__imagen {
  width: 10rem;
  margin-left: 10rem; }

.tooltip {
  color: #7cb342;
  position: relative; }

.tooltip:hover {
  text-decoration: underline; }

.tooltip:hover .tooltip-box {
  display: inline-block; }

.tooltip-box {
  display: none;
  position: absolute;
  background: #000000;
  line-height: 2rem;
  z-index: 500;
  text-align: center;
  color: #ffffff;
  font-size: 1.4rem;
  padding: 5px 10px;
  border-radius: 5px;
  left: 0;
  right: 0;
  bottom: 30px; }

.tooltip-box::after {
  content: "";
  display: block;
  border-top: 7px solid #000000;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  position: absolute;
  bottom: -7px;
  left: calc(50% - 7px); }

